<template>
  <div class="tab-control">
    <div
      v-for="(item,index) in titles"
      :key="item.index"
      class="tab-control-item"
      :class="{active: index === currentIndex}"
      @click="activeclick(index)">
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TabControl",
  props: {
    titles: {
      type: Array,
      default: [],
    },
    tabgoods:{
      type:Object,
      default:{}
    }
  },
  data() {
    return {
      currentIndex: 0,
      tab:[]
    };
  },
  components: {},
  mounted() {},
  created(){
    for(let key in this.tabgoods){
        this.tab.push(key)
      }
  },
  methods: {
    activeclick(index) {
      //判断下标是否相等
      this.currentIndex = index;
      this.$emit('listtab',this.tab[index],index)
    },
  },
  computed: {},
};
</script>

<style scoped>
.tab-control {
  display: flex;
  text-align: center;
  font-size: 15px;
  height: 40px;
  line-height: 40px;
  background: #ffffff;
}
.tab-control-item {
  flex: 1;
}
.tab-control-item span {
  padding: 5px;
}
.active {
  color: var(--color-high-text);
  
}
.active span {
  border-bottom: pink solid 3px;
}
</style>