<template>
  <div class="good-list-item">
    <img :src="gooditem.show.img" alt="" @load='imgload' @click='clickimg'/>
    <p>{{gooditem.title}}</p>
    <div class="span-time">
      <span>{{gooditem.price}}</span>
      <span class="collect">{{gooditem.cfav}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "GoodsListItem",
  props: {
    gooditem: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {};
  },
  components: {},
  mounted() {},
  methods: {
    imgload(){
      // $bus 事件总线
      this.$bus.$emit('itemimgload')
    },
    clickimg(){
      //事件总线发送点击的id出去
      // this.$bus.$emit('senddetail',this.gooditem.iid)
      this.$router.push('/detail/'+ this.gooditem.iid)
      console.log(this.gooditem.iid);
    }
  },
};
</script>

<style scoped>
.good-list-item {
  width: 48%;
  padding-bottom: 20px;
}
.good-list-item img {
  width: 100%;
  flex: 1;
  border-radius: 5px;
}
.good-list-item p {
  /* 超出的文字部分将省略 */
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 3px;
}
.span-time {
  text-align: center;
}
.span-time :nth-child(1) {
  margin-right: 5px;
  color: var(--color-high-text);
}
.span-time .collect::before{
  display: inline-block;
  content: "";
  width: 15px;
  height: 13px;
  background: url(~assets/img/common/collect.svg) no-repeat;
  background-size: 15px;
}
</style>