<template>
  <div id="home">
    <navbar class="home-nav"><div slot="center">蘑菇街</div></navbar>
    <tab-control :titles="['流行','新款','精选']" class="tab-control flexcontrol" 
        :tabgoods='goods' @listtab='fn' ref='tabControl1'
        :class="{flex:isTabFixed}" v-show='isTabFixed'/>

    <better-scroll class="content" ref='scroll' 
                    :probe-type='3'
                    @scroll='contentscroll'
                    :pull-up-load='true'
                    @pullingUp='loadmore'>
        <home-swiper :banner="banner"/>
        <home-yang :recommend="recommend"/>
        <fashion />
        <tab-control :titles="['流行','新款','精选']" class="tab-control" 
        :tabgoods='goods' @listtab='fn' ref='tabControl2'
        :class="{flex:isTabFixed}" />
        <goods-list :goodslist="goods[this.value].list"/>
    </better-scroll>
    <div>
      <back-top @click.native='backClick' v-show='isshow'/>
    </div>
  </div>
</template>
<script>
import HomeSwiper from "./childComps/HomeSwiper";
import HomeYang from "./childComps/HomeYang";
import Fashion from "./childComps/Fashion";

import Navbar from "components/common/navbar/NavBar";
import TabControl from "components/content/tabControl/TabControl";
import GoodsList from 'components/content/goods/GoodsList';
import BetterScroll from 'components/common/scroll/BetterScroll';
import BackTop from '../../components/content/backtop/BackTop';

import {gethomedata, gethomedatalist} from "network/home";
import { debounce } from "common/utils";

export default {
  name: "Home",
  data() {
    return {
      banner: [],
      recommend: [],
      goods: {
        "pop": { page: 0, list: [] },
        "new": { page: 0, list: [] },
        "sell": { page: 0, list: [] },
      },
      value:'pop',
      isshow:false,
      tabsetTop:0,
      isTabFixed:false,
      saveY:0,
      deatil:null
    };
  },
  components: {
    HomeSwiper,
    HomeYang,
    Fashion,
    Navbar,
    TabControl,
    GoodsList,
    BetterScroll,
    BackTop
  },

  created() {
    console.log('home-created');
    //请求轮播图数据
    this.gethomedata()
    //请求商品数据
    this.gethomedatalist("pop");
    this.gethomedatalist("new");
    this.gethomedatalist("sell");
  },
  
  mounted() {
    // const refresh = debounce(this.$refs.scroll.scroll.refresh)
    //拿到GoodsListItem的事件总线,监听item中的图片加载完成
    // this.$bus.$on('itemimgload',() =>{
    //   console.log('-----------');
    //   //拿到数据再次刷新
    //   this.$refs.scroll && this.$refs.scroll.refresh()
    // })

    this.$bus.$on('itemimgload', debounce(() =>{
      console.log('-----------');
      // 拿到数据再次刷新
      this.$refs.scroll && this.$refs.scroll.refresh()
    }))


    // 拿到tabControl的offsetTop值
    // 用$refs拿到组件，每个组件中都有一个$el组件，通过可以拿到组件中的元素
    setTimeout(() => {
      //拿到流行新款等导航栏的高度
      this.tabsetTop = this.$refs.tabControl2.$el.offsetTop;
      // console.log(this.tabsetTop);
    }, 220);
    
  },
  destroyed(){
    console.log('destroyed');
  },

  // 当前活跃路由
  activated(){
    this.$refs.scroll.scroll.refresh()
    this.$refs.scroll.scroll.scrollTo(0,this.saveY)
   
  },
  //离开路由时记录参数
  deactivated(){
    this.saveY=-(-this.$refs.scroll.scroll.y);
  },
  
  methods: {
    /**
     * 网络请求相关的
     */
    gethomedata(){
        gethomedata().then((res) => {
          this.banner = res.data.banner.list;
          this.recommend = res.data.recommend.list;  
    });
    },
    gethomedatalist(type) {
      //获取页数
      const page = this.goods[type].page + 1
      gethomedatalist(type, page).then((res) => {
        // 俩种方式遍历数组存储到对象里
        //第一种循环遍历push进去
        // for(let n of res.data.list){
        //   this.goods[type].list.push(n)
        // }

        // 第二种使用...解析push进去
        this.goods[type].list.push(...res.data.list)
        //请求完后页码加1
        this.goods[type].page += 1

        //多次请求页面数据
        // this.$refs.scroll 判断是否有数据
        this.$refs.scroll && this.$refs.scroll.scroll.finishPullUp()
      });
    },

    /**
     * 事件监听相关的
     */
    fn(param,index){
      this.value=param
      // console.log(param);
      // 拿到下标对比位置
      this.$refs.tabControl1.currentIndex = index
      this.$refs.tabControl2.currentIndex = index
    },
    backClick(){
      // scrollTo：三个参数  x,y,需要的时间 
      this.$refs.scroll.scroll.scrollTo(0,0,500)
    },
    contentscroll(position){

      // 判断滑动大小是否显示back-top返回顶部元素
      this.isshow = (position.y) < -1000 ? true:false

      //判断tabcontrol的位置动态的改变是否吸顶
      this.isTabFixed = (this.tabsetTop-44) < (-position.y)
      
    },
    loadmore(){
      console.log('加载更多');
      //滑动到底部请求更多数据
      this.gethomedatalist(this.value)
    },
   

  },


};
</script>

<style  scoped>
.home-nav {
  text-align: center;
  background-color: var(--color-tint);
  color: white;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1; 
}
.tab-control {
  position: sticky;
  top: 44px;
  z-index: 99;
}
.flex{
  position:fixed;
  top: 44px;
  right: 0;
  left: 0;

}
#home{
  /* vh视口高度 */
  height: 100vh;
}
/*设置高度是为了让content高度大于wrapper高度，否则无滚动效果*/
.content{
  height: calc(100% - 49px);
  overflow: hidden;
  /* //滚动条 */
  /* overflow-y:scroll */
}
</style>